.cards-slider-wrapper {
    display: flex;
    position: relative;
    width: 100px;
    transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    
    .card {
        flex: 1;
        transform: scale(0.7);
        transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
        min-width: 200px;
        opacity: 0.5;
        transition: opacity 30ms linear;
    }
}

.cards-slider {
    position: relative;
    max-width: 100px;
    margin: 0 auto;
}

.cards-slider::after {
    content: '';
    display: block;
    width: 150px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
}